.table__row-info{
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 3rem;
  height: 100%;
  border: 1px solid #eee;
  background-color: #f8f9fa;
  font-size: 1.6rem;
  border-top: 0;
  border-right: 0;
  position: relative;
}

.table__row-info:first-child{
  border-right: 1px solid #eee;
}